// src/components/BudgetList/BudgetList.tsx
"use client";

import { useEffect } from "react";
import { useBudget } from "../../context/BudgetContext"; // useBudget hook'u
import styles from './BudgetList.module.css';

export default function BudgetList() {
  const { transactions, setTransactions,  } = useBudget(); // Verileri context'ten alıyoruz

  useEffect(() => {
    // Burada sadece verileri Context API'ye alıyoruz, LocalStorage ile ilgili işlem yapılmaz
  }, [setTransactions]);

  return (
    <div className={styles["list-container"]}>
  <h1>Bütçe Listesi</h1>
  {/* Bütçe limiti gösterimi */} 

  {transactions.length === 0 ? (
    <p className={styles["empty-message"]}>Henüz eklenmiş bir işlem yok.</p>
  ) : (
    <table className={styles["table"]}>
      <thead>
        <tr>
          <th>Tip</th>
          <th>Kategori</th>
          <th>Açıklama</th>
          <th>Tutar</th>
          <th>Tarih</th>
        </tr>
      </thead>
      <tbody>
        {transactions.map((transaction) => (
          <tr key={transaction.id}>
            <td>{transaction.type === "income" ? "Gelir" : "Gider"}</td>
            <td>{transaction.category}</td>
            <td>{transaction.description}</td>
            <td>{transaction.amount.toFixed(2)} TL</td>
            <td>{transaction.date}</td>
          </tr>
        ))}
      </tbody>
    </table>
  )}
</div>

  );
}
