"use client";

import { useState } from "react";
import { BudgetProvider } from "./context/BudgetContext";
import BudgetForm from "./components/BudgetForm/BudgetForm";
import BudgetList from "./components/BudgetList/BudgetList";
import ChartPage from "./components/Chart/Chart";
import CategoryForm from "./components/Category/Category";
import { CategoryProvider } from "./context/CategoryContext";
import styles from "./page.module.css";

import Box from "@mui/material/Box";
import RadioGroup from "@mui/material/RadioGroup";
import Radio from "@mui/material/Radio";
import FormControl from "@mui/material/FormControl";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormLabel from "@mui/material/FormLabel";
import { ThemeProvider, createTheme } from "@mui/material/styles";

// Tema geçiş fonksiyonu
 

export default function Home() {
  const [activeComponent, setActiveComponent] = useState<
    "form" | "list" | "chart" | "category" | null
  >("form");
  const [mode, setMode] = useState<"light" | "dark" | "system">("system");

  // Tema ayarları
  const currentTheme = createTheme({
    palette: {
      mode: mode === "system" ? "light" : mode, // Sistem tercihi ile mod ayarlanır.
    },
  });

  return (
    // ThemeProvider tüm sayfayı kapsayacak şekilde yerleştirildi
    <ThemeProvider theme={currentTheme}>
      <BudgetProvider>
        <CategoryProvider>
          <main className={styles.container}>
            <h1 className="text-2xl font-bold mb-4">
              Kişisel Bütçe Uygulaması
            </h1>

            {/* Tema değiştirme butonu */}
           

            <div className={styles.buttonContainer}>
              <button
                className={styles.button}
                onClick={() => setActiveComponent("form")}
              >
                Bütçe Formu
              </button>
              <button
                className={styles.button}
                onClick={() => setActiveComponent("list")}
              >
                Bütçe Listesi
              </button>
              <button
                className={styles.button}
                onClick={() => setActiveComponent("category")}
              >
                Kategoriler
              </button>
              <button
                className={styles.button}
                onClick={() => setActiveComponent("chart")}
              >
                Grafikler
              </button>
            </div>

            {/* Seçilen component'i göster */}
            {activeComponent === "form" && <BudgetForm />}
            {activeComponent === "list" && <BudgetList />}
            {activeComponent === "chart" && <ChartPage />}
            {activeComponent === "category" && <CategoryForm />}
          </main>
        </CategoryProvider>
      </BudgetProvider>
    </ThemeProvider>
  );
}
