"use client";

import React, { useState } from "react";
import { useCategory } from "../../context/CategoryContext";
import styles from "./CategoryForm.module.css";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css"; // CSS importu

const CategoryForm = () => {
  const [name, setName] = useState("");
  const [budget, setBudget] = useState<number>(0);
  const [editIndex, setEditIndex] = useState<number | null>(null);
  const { categories, addCategory, updateCategory, removeCategory } =
    useCategory();

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (editIndex !== null) {
      // Kategoriyi güncelle
      updateCategory(editIndex, { name, budget });
      setName("");
      setBudget(0);
      setEditIndex(null);

      // Güncelleme bildirimi
      toast.success("Kategori başarıyla güncellendi!");
    } else if (name && budget > 0) {
      // Yeni kategori ekle
      addCategory({ name, budget });
      setName("");
      setBudget(0);

      // Ekleme bildirimi
      toast.success("Kategori başarıyla eklendi!");
    }
  };

  const handleDelete = (index: number) => {
    removeCategory(index);
    // Silme bildirimi
    toast.error("Kategori başarıyla silindi!");
  };

  const handleEdit = (index: number) => {
    setName(categories[index]?.name || ""); // Ensure category exists
    setBudget(categories[index]?.budget || 0); // Ensure budget is valid
    setEditIndex(index);
  };

  return (
    <div className={styles.container}>
      <h2>Kategoriler</h2>
      <form onSubmit={handleSubmit} className={styles.form}>
        <input
          type="text"
          placeholder="Kategori Adı"
          value={name}
          onChange={(e) => setName(e.target.value)}
          className={styles.input}
        />
        <input
          type="number"
          placeholder="Bütçe"
          value={budget}
          onChange={(e) => setBudget(Number(e.target.value))}
          className={styles.input}
        />
        <button type="submit" className={styles.button}>
          {editIndex !== null ? "Güncelle" : "Ekle"}
        </button>
      </form>
      <table className={styles.table}>
        <thead>
          <tr>
            <th>Kategori Adı</th>
            <th>Bütçe Limiti</th>
            <th>Düzenle</th>
            <th>Sil</th>
          </tr>
        </thead>
        <tbody>
          {Array.isArray(categories) && categories.length > 0 ? (
            categories.map((category, index) => (
              <tr key={index}>
                <td>{category?.name || "Bilinmiyor"}</td> {/* Fallback for missing name */}
                <td>{category?.budget ? `${category.budget}₺` : "Bilinmiyor"}</td> {/* Fallback for missing budget */}
                <td>
                  <button
                    className={styles.editButton}
                    onClick={() => handleEdit(index)}
                  >
                    Güncelle
                  </button>
                </td>
                <td>
                  <button
                    className={styles.deleteButton}
                    onClick={() => handleDelete(index)}
                  >
                    Sil
                  </button>
                </td>
              </tr>
            ))
          ) : (
            <tr>
              <td colSpan={4} className={styles.noData}>
                Kategoriler bulunamadı
              </td>
            </tr>
          )}
        </tbody>
      </table>

      {/* ToastContainer'ı ekliyoruz */}
      <ToastContainer position="top-right" autoClose={3000} hideProgressBar />
    </div>
  );
};

export default CategoryForm;
